/* mobile navbar is starting from here */
.nav-bar1{
    height: 100vh;
    width:100%;
   position: fixed;
    background-color:#282c34;
   transform: translateX(-100%);
    display:flex;
    justify-content:center;
    align-items: center;
    flex-direction: column;
   
}
.trans{
    transform: translateX(0);
    transition: all 0.5s;
}
.tran{
   
    transition: all 0.5s;
}
.nav-link1{
    text-decoration: none;
    color:white;
    font-size: 1.2rem;
   font-family: monospace;
   padding:1rem;
 border-bottom: 1px solid rgb(104, 104, 104);
    margin:2rem;
}
.register1{
    background-color: green;
    padding:0.5rem;
    color:white;
    padding-left: 2.1rem;
    padding-right: 2.1rem;
    border-radius: 0.3rem;
    animation: register 1.2s linear infinite;
}
.register1:hover{
    color:white;
}
.login1:hover{
    color:white;
}
/* animation */
@keyframes register{
    0%{
        background-color:green;
    }
    50%{
        background-color:rgb(0, 213, 0);
    }
    100%{
        background-color:green;
    }
}
@keyframes login{
    0%{
        background-color:blue;
    }
    50%{
        background-color:rgb(35, 126, 245);
    }
    100%{
        background-color:blue;
    }
}
.login1{
    border-radius: 0.3rem;
    
  
    padding:0.5rem;
    padding-left: 3rem;
    padding-right: 3rem;
    color:white;
    animation: login 1.2s linear infinite;
}

.nav2{
    border-bottom: 1px solid rgb(57, 57, 57);
    width:100%;
    height: 10vh;
    display: flex;
   background-color: #282c34;
   position: fixed;
        box-shadow: 0 2px 2px -2px rgba(0,0,0,.3);
    
    display: none;
}
